import React from 'react';
import './current-course.scss';
import urrentourseItem from "../urrentourseItem/urrentourseItem";

const urrentourseContainer = () => {
    return (
        <article className='current-course'>
            <urrentourseItem title='USD/UAH' param='USD_UAH'/>
            <urrentourseItem title='EUR/UAH' param='EUR_UAH'/>
        </article>
    );
};

export default urrentourseContainer;